import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import HomePage from './HomePage';
import RevendaPage from './RevendaPage';
import FestasPage from './FestasPage';
import ConfirmPage from './ConfirmPage';
import Pagamento from './pagamento';
import Checkout from './checkout';
import { HashRouter as Router, Routes, Route } from 'react-router-dom';

function Cancelado() {
  return (
    <main className="min-h-screen bg-gray-900 text-white flex items-center justify-center">
      <div className="text-center">
        <h1 className="text-3xl font-bold mb-2">Operao cancelada</h1>
        <p className="text-gray-400">Voc recusou a transao.</p>
      </div>
    </main>
  );
}

function Aguardando() {
  return (
    <main className="min-h-screen bg-gray-900 text-white flex items-center justify-center">
      <div className="text-center">
        <h1 className="text-3xl font-bold mb-2">Aguardando pagamento do outro usurio</h1>
        <p className="text-gray-400">Voc ser notificado quando o pagamento for concludo.</p>
      </div>
    </main>
  );
}

ReactDOM.createRoot(document.getElementById('root')).render(
  <React.StrictMode>
    <Router>
      <Routes>
        <Route path="/" element={<HomePage />} />
        <Route path="/festas" element={<FestasPage />} />
        <Route path="/revenda" element={<RevendaPage />} />
        <Route path="/checkout" element={<Checkout />} />
        <Route path="/confirmar" element={<ConfirmPage />} />
        <Route path="/pagamento" element={<Pagamento />} />
        <Route path="/cancelado" element={<Cancelado />} />
        <Route path="/aguardando" element={<Aguardando />} />
      </Routes>
    </Router>
  </React.StrictMode>
);
